const setTitle = function(t) {
  console.log('setTitle')
  // var t = document.querySelectorAll('[_title]')[document.querySelectorAll('[_title]').length-1].attributes['_title'].nodeValue;
  // var t2 = document.querySelectorAll('[_title]')[document.querySelectorAll('[_title]').length-1].attributes['_title'].nodeValue;
  document.title = t;
  var i = document.createElement('iframe');
  i.src = '//m.baidu.com/favicon.ico';
  i.style.display = 'none';
  i.onload = function() {
    setTimeout(function(){
      i.remove();
    }, 9)
  }
  document.body.appendChild(i);
}



